import pt_br from '../lang/pt-br.json';
import en from '../lang/en.json';
export const langHandler = async (prompt) => {
    let jsonLang;
    const questions = [
        {
            type: 'list',
            name: 'action',
            message: 'Hello, please select your preferred language:',
            choices: [
                {
                    name: `English (Default)`,
                    value: () => {
                        jsonLang = langPicker('en');
                    }
                },
                {
                    name: 'Portugus do Brasil',
                    value: () => {
                        jsonLang = langPicker('pt_br');
                    }
                }
            ]
        }
    ];
    await prompt(questions).then((answer) => { answer.action(); });
    return jsonLang;
};
const langPicker = (language) => {
    return language === 'pt_br' ? pt_br : en;
};
